import React, { Component } from 'react'
import './App.css'
import Clock from './components/Clock.js'
import ReactGA from 'react-ga'

import packageJson from '../package.json'

class App extends Component {
  componentDidMount () {
    try {
      ReactGA.initialize('UA-128443894-1');
      ReactGA.pageview(window.location.pathname + window.location.search);
    } catch(e) {
      console.log("Analytics Couldn't load: ", e)
    }
  }

  render () {
    return (
      <div className="App">
        <header>
          <span>Forza Horizon 4 Seasons Clock</span>
        </header>
        <main>
        <Clock />
        </main>
        <footer>
          <span>Version: {packageJson.version}</span>
          <div>
            <span>Report an issue.</span>
            <a className="github-button" 
              href="https://github.com/forzahorizon4seasonsclock/forzahorizon4seasonsclock.github.io/issues"
              data-icon="octicon-issue-opened"
              data-size="large"
              aria-label="Issue forzahorizon4seasonsclock/forzahorizon4seasonsclock.github.io on GitHub">Issue</a>
          </div>
        </footer>
      </div>
    );
  }
}

export default App
